import "./App.css";
import { TechIconSelector } from "./Icons";
import { NavBar } from "./Navbar";
import { useTranslation } from "react-i18next";
import { Popover, Whisper } from "rsuite";


function CardTag(props: { tag: string }) {
  const { t, i18n } = useTranslation();
  return (
    <span className="text-white bg-gray-500/80 px-2 rounded-full mx-2">
      #{t(props.tag)}
    </span>
  );
}

const speakerSelector = (tech: string) => {
  return (
    <Popover
      arrow={false}
      className="backdrop-blur-md bg-white/30 dark:text-white text-black"
      title={tech}
    />
  );
}

function CardTags(props: { tags: string[] }) {
  return (
    <div className="flex flex-wrap justify-center gap-y-2">
      {props.tags.map((tag) => (

          <CardTag key={tag} tag={tag} />
      ))}
    </div>
  );
}

function TechUsed(props: { techUsed: string[] }) {
  return (
    <div className="flex flex-wrap self-center items-center gap-x-2 gap-y-1 justify-end w-full mx-3 mt-4 mb-2 dark:invert invert-0">
      {props.techUsed.map((tech) => (
        <Whisper
          placement="top"
          trigger="hover"
          speaker={speakerSelector(tech)}
        >
          <div>
            <>{TechIconSelector(tech)}</>
          </div>
        </Whisper>
      ))}
    </div>
  );
}

interface IProjectInfo {
  title: string;
  techUsed: string[];
  tags: string[];
  link: string;
  name: string;
}

const projects: IProjectInfo[] = [
  {
    title: "Control for UR5",
    techUsed: ["React", "Express", "Typescript", "SocketIO", "Arduino"],
    tags: ["IoT", "Robotics", "Control"],
    link: "https://github.com/Pondigo/webSocket_UR5_esp32_driver",
    name: "control",
  },
  {
    title: "Onnedrive backup tool",
    techUsed: ["React", "Express", "Typescript"],
    tags: ["Onedrive", "ms-graph", "backup_tool"],
    link: "https://github.com/Pondigo/Backup_Onedrive_Server-Client",
    name: "backup",
  },
];



function ProjectCard(props: { project: IProjectInfo }) {
  const { t, i18n } = useTranslation();

  //Opem on new tab the link on props.project.link
  const handleLinkClick = (event: React.MouseEvent<HTMLDivElement>) => {
    //console.log("Clicked");
    event.preventDefault();
    window.open(props.project.link, "_blank");
  };

  return (
    <div onClick={handleLinkClick} className="w-1/2 sm:w-1/3 md:w-1/4 lg:w-1/5 px-2 cursor-pointer">
      <div className="flex flex-col dark:bg-white/10 bg-gray-500/20 backdrop-blur-md items-center justify-center p-2 rounded-lg">
        <img src={"iconsUI/"+props.project.name+".png"} alt={props.project.name} className="w-11/12" />
        <h4 className="dark:text-white text-base leading-10 mb-2 mt-1 text-black">
          {t(props.project.title)}
        </h4>
        <CardTags tags={props.project.tags} />
        <TechUsed techUsed={props.project.techUsed} />
      </div>
    </div>
  );
}

function PortraitBody() {
  const { t, i18n } = useTranslation();
  return (
    <div>
      <div className="text-center">
        <h1 className="leading-none text-black dark:text-white lg:text-lg">
          {t("My projects")}
        </h1>
      </div>
      <div>
        <h3 className="text-black dark:text-white pl-5 lg:text-base lg:mt-5 md:mb-2">
          {t("Web development")}
        </h3>
        <div className="flex flex-wrap gap-y-4">
          <ProjectCard project={projects[0]} />
          <ProjectCard project={projects[1]} />
   
         
        </div>
      </div>
    </div>
  );
}

export default function App() {
  return (
    <div className="overflow-x-hidden overflow-y-auto pb-5 absolute w-screen h-screen  bg-gradient-to-r from-rose-100 to-teal-150 dark:from-gray-700 dark:via-gray-900 dark:to-black">
      <NavBar />
      <PortraitBody />
    </div>
  );
}
