import { useState } from 'react';
import { ToggleDarkMode } from "./ToggleDarkMode";
import { useTranslation } from "react-i18next";

export const NavBar = () => {
  const [enabled, setEnabled] = useState(false);
  const { t, i18n } = useTranslation();
  const [spanish , setSpanish ] = useState(false)
  //Toogle lengauge (en/es)
  const handleLanguageChange = () => {
    if (spanish) {
      i18n.changeLanguage("en");
      setSpanish(false);
    } else {
      i18n.changeLanguage("es");
      setSpanish(true);
    }
  }

  return (
    <div className="flex items-center m-1 justify-between">
      <div className="flex items-center dark:text-gray-50 text-black">
        <img
          src={enabled ? "iconsUI/logo_dt.svg" : "iconsUI/logo_wt.svg"}
          alt="logo"
          className="w-20 h-20 pl-4 pr-4"
        />
        <a
          href="https://github.com/Pondigo"
          className="md:px-4 px-1 hover:text-green-800 dark:hover:text-green-400 hover:font-semibold hover:no-underline"
        >
          Github
        </a>
        <a
          href="https://www.linkedin.com/in/lenora-pondigo-santamaria-703132239"
          className="md:px-4 px-1 hover:text-green-800 dark:hover:text-green-400 hover:font-semibold hover:no-underline"
        >
          LinkedIn
        </a>
      </div>
      <div className="flex md:pr-4 flex-wrap items-center self-center text-center justify-around">
        <ToggleDarkMode enabled={enabled} setEnabled={setEnabled} />
        <button
          className="text-center w-full text-black dark:text-white"
          onClick={handleLanguageChange}
        >
          <span className="flex mt-1 justify-center">
            <p className={spanish ? "" : "opacity-50"}>ES</p> /{" "}
            <p className={spanish ? "m-0 opacity-50" : "m-0"}>EN</p>
          </span>
        </button>
      </div>
    </div>
  );
};
