import { SocketIOIcon } from "./SocketIO";
import { ArduinoIcon } from "./Arduino";
import { ReactIcon } from "./React";
import { TypescriptIcon } from "./Typescript";
import { ExpressIcon } from "./Express";


export const TechIconSelector = (tech: string) => {
    switch (tech) {
        case "React":
        return <ReactIcon/>;
        case "Typescript":
        return <TypescriptIcon/>;
        case "Express":
        return <ExpressIcon/>;
        case "SocketIO":
        return <SocketIOIcon/>;
        case "Arduino":
        return <ArduinoIcon/>;
        default:
        return <ReactIcon/>;
    }
}

